import { Component } from '@angular/core';
import { Injectable } from '@angular/core';
import { AmazonAuthResponse } from 'angular-amazon-login';
import { AmazonLoginComponent } from 'angular-amazon-login';
import { Amazonservices } from './amazonservices';
import { Router, NavigationStart, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
@Injectable()
export class AppComponent {
  title = 'Pillenbox-Konfigurator';
  
  public showWaitInfo: boolean = false;
  private myClientId: string = 'amzn1.application-oa2-client.a9e64c41743f4d14813be90d8f38cf9f';

  constructor(private amService: Amazonservices, private router: Router) {
    router.events.subscribe(
      (event) => {

        if (event instanceof NavigationStart) {
          this.showWaitInfo = true;
        }
        if (event instanceof NavigationEnd
          || event instanceof NavigationCancel
          || event instanceof NavigationError) {
          this.showWaitInfo = false;
        }

      }
    );
  }

  goHome() {
    this.router.navigate(['/home']);
  }

  onAmazonAuthResponse(event: AmazonAuthResponse) {
    if (event.authRequest.error === undefined) {
      
      console.log(event);

      this.amService.getUserProfile(event.authRequest.access_token);



    }
  }
}




